.App {
  height: 100vh;
  font-size: 30px;
  text-align: center;
  font-family: Consoles;
  background-color: #FF00FF;
}

.navbar {
  align-self: center;
  font-family: Consoles;
}
.nav_link {
  color: #FFFFFF;
  border-radius: 5px;
  text-decoration: none;
  transition: background-color 2s ease-out 100ms, color 2s ease-out 100ms;
}
.nav_link:hover {
  color :#c500c5;
  background-color :#ffb8ff;
}

.home{
  color:#7f0099;
}

.link, .link_youtube, .link_soundcloud, .link_github, .link_twitter {
  padding: 15px;
  color:#7f0099;
  font-size: 30px;
  border-radius: 25px;
  text-decoration: none;
  transition: background-color 2s ease-out 100ms, color 2s ease-out 100ms, font-size 1s ease-out 10ms;
}
.link:hover, .link_youtube:hover, .link_soundcloud:hover, .link_github:hover, .link_twitter:hover {
  font-size: 40px;
}
.link_youtube:hover{
  color:#FFFFFF;
  background-color:#FF0000;
}
.link_soundcloud:hover{
  color:#FFFFFF;
  background-color:#F26F23;
}
.link_github:hover{
  color:#fafbfc;
  background-color:#24292e;
}
.link_twitter:hover{
  color:#F5F8FA;
  background-color:#1DA1F2;
}
